import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import { Button, Form, Modal, Row } from "react-bootstrap"
import { Formik } from "formik"
import * as Yup from "yup"
import clsx from "clsx"

import employeesApi from '~/api/employeesApi'
import MultiSelect from '~/components/MultiSelect'

const propTypes = {
    visible: PropTypes.bool.isRequired,
    setVisible: PropTypes.func.isRequired,
    proposalType: PropTypes.object
}

const FormSubmitProposalType = ({ visible, setVisible, proposalType }) => {
    const [creatorList, setCreatorList] = useState([])

    useEffect(() => {
        employeesApi.getEmployeeListByName("")
            .then((response) => {
                setCreatorList(response.data.data.employees)
            })
    }, [])

    const handleCreatorSearch = (value) => {
        employeesApi.getEmployeeListByName(value)
            .then((response) => {
                setCreatorList(response.data.data.employees)
            })
    }

    /* Xử lý Form với Formik */
    let initialValues = {
        name: "",
        subsystem: "",
        creators: [],
        repeat: 1
    }
    if (proposalType?.id) {
        initialValues = proposalType
    }

    const validationSchema = Yup.object({
        name: Yup.string().required("Vui lòng nhập tên loại đề xuất."),
        subsystem: Yup.string().required("Vui lòng chọn phân hệ"),
        creators: Yup.array(),
        repeat: Yup.number().required()
    })

    const handleSubmit = async (values, actions) => {
        actions.setSubmitting(true)
        if (proposalType?.id) {

        }
        else {

        }
        setVisible(false)
        actions.setSubmitting(false)
    }
    //
    return (
        <Modal
            size="md"
            scrollable
            centered
            show={visible}
            onHide={() => setVisible(false)}
        >
            <Modal.Header closeButton>
                <Modal.Title>
                    {proposalType?.id ? "Chỉnh sửa loại đề xuất" : "Tạo loại đề xuất"}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    onSubmit={handleSubmit}
                >
                    {
                        ({ values, touched, errors, handleChange, handleBlur, handleSubmit, setFieldValue, isValid, dirty }) => (
                            <Form onSubmit={handleSubmit}>
                                <div className="mb-4">
                                    <Form.Label>Tên loại đề xuất:</Form.Label>
                                    <Form.Control
                                        type="text"
                                        name="name"
                                        placeholder="Nhập tên loại đề xuất"
                                        className={clsx({
                                            "is-invalid": touched.name && errors.name
                                        })}
                                        value={values.name}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    />
                                    {
                                        touched.name && errors.name && <div className="invalid-feedback">{errors.name}</div>
                                    }
                                </div>
                                <div className="mb-4">
                                    <Form.Label>Phân hệ:</Form.Label>
                                    <Form.Select
                                        name="subsystem"
                                        className={clsx({
                                            "is-invalid": touched.name && errors.name
                                        })}
                                        value={values.subsystem}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    >
                                        <option value="Chung">Chung</option>
                                        <option value="Thiết bị">Thiết bị</option>
                                    </Form.Select>
                                    {
                                        touched.subsystem && errors.subsystem && <div className="invalid-feedback">{errors.subsystem}</div>
                                    }
                                </div>
                                <div className="mb-4">
                                    <Form.Label>Người được phép tạo đề xuất:</Form.Label>
                                    <MultiSelect
                                        placeholder="Chọn người được phép tạo đề xuất..."
                                        displayValue="name"
                                        showCheckbox
                                        options={creatorList}
                                        selectedValues={values.creators}
                                        onSelect={(selectedList) => {
                                            setFieldValue("creators", selectedList)
                                        }}
                                        onRemove={(selectedList) => {
                                            setFieldValue("creators", selectedList)
                                        }}
                                        onSearch={handleCreatorSearch}
                                    />
                                </div>
                                <div className="mb-4">
                                    <Form.Label>Lặp lại:</Form.Label>
                                    <Row className="flex-column justify-content-center ms-1">
                                        <Form.Check
                                            type="radio"
                                            name="repeat"
                                            label="Liên tục"
                                            className="col"
                                            checked={values.repeat === 1}
                                            onChange={() => {
                                                setFieldValue("repeat", 1)
                                            }}
                                        />
                                        <Form.Check
                                            type="radio"
                                            name="repeat"
                                            className="col"
                                            label="Lặp lại hàng tháng"
                                            checked={values.repeat === 2}
                                            onChange={() => {
                                                setFieldValue("repeat", 2)
                                            }}
                                        />
                                        <Form.Check
                                            type="radio"
                                            name="repeat"
                                            className="col"
                                            label="Lặp lại theo năm"
                                            checked={values.repeat === 3}
                                            onChange={() => {
                                                setFieldValue("repeat", 3)
                                            }}
                                        />
                                    </Row>
                                </div>
                                <Button
                                    size="lg"
                                    type="submit"
                                    className="d-table m-auto"
                                    disabled={!(dirty && isValid)}>
                                    {proposalType?.id ? "Cập nhật" : "Lưu"}
                                </Button>
                            </Form>
                        )
                    }
                </Formik>
            </Modal.Body>
        </Modal>
    )
}

FormSubmitProposalType.propTypes = propTypes

export default FormSubmitProposalType